import {default as MockHost} from '../mock/clientSideWebParts/MockHost';
import MockClientSideWebPartManifests from '../mock/clientSideWebParts/MockClientSideWebPartManifests';
import ClassicPageBootstrapper from '../../local/clientSideWebParts/core/classicPages/ClassicPageBootstrapper';

// Make sure the classic page bootstrapper is loaded before it gets used.
/* tslint:disable:no-any */
if (!(window as any).g_cswp) {
  (window as any).g_cswp = new ClassicPageBootstrapper();
/* tslint:enable:no-any */
}

/* tslint:disable:no-any */
(window as any).g_cswp.loadClientSideWebPartOnTestPage =
  (id: string, instanceId: string, manifest: any, data: any): void => {
    (window as any).g_cswp.loadClientSideWebPart(instanceId,
                                                 MockClientSideWebPartManifests.getManifest(id),
                                                 data,
                                                 MockHost);
/* tslint:enable:no-any */
};
